::-webkit-scrollbar {
  width: 10px;
  background: #ccc; }

::-webkit-scrollbar-thumb {
  background: #6161A9;
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }

.wrapper {
  margin-right: auto;
  margin-left: auto; }

.wrapper-md {
  height: 100%;
  max-width: 1200px;
  position: relative;
  margin-right: auto;
  margin-left: auto; }
  @media (max-width: 1220px) {
    .wrapper-md {
      padding: 0 20px; } }

ul.index {
  margin-top: 20px; }

.data-title,
.data-description,
.data-link {
  color: #fff; }

@media (min-width: 481px) {
  .data-description {
    font-size: 18px;
    line-height: 24px; } }

@media (max-width: 480px) {
  .data-description {
    font-size: 16px;
    line-height: 22px; } }

.data-description span {
  font-size: 16px;
  max-height: 30px;
  font-weight: bold;
  background: #fff;
  border-radius: 50px;
  padding: 2px 4px;
  color: #000;
  display: inline-block; }

.data-subtitle {
  font-size: 16px;
  line-height: 23px;
  color: #ddd;
  margin-bottom: 5px;
  display: inline-block; }

#header,
#footer {
  width: 100%;
  background: #00557b; }

.content {
  flex-basis: 100%; }

.search-area {
  height: 100%;
  text-align: center;
  border: dashed 1px #6161A9;
  border-radius: 10px;
  margin-top: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transition: border ease-in-out 0.4s;
  transition: border ease-in-out 0.4s; }
  @media (min-width: 481px) {
    .search-area {
      max-height: 560px; } }
  @media (max-width: 480px) {
    .search-area {
      max-height: 440px;
      padding-right: 20px;
      padding-left: 20px; } }
  .search-area > h3 {
    max-width: 700px;
    color: #6161A9;
    -webkit-transition: color ease-in-out 0.2s;
    transition: color ease-in-out 0.2s; }
    @media (min-width: 401px) {
      .search-area > h3 {
        font-size: 27px; } }
    @media (max-width: 400px) {
      .search-area > h3 {
        font-size: 24px; } }
  .search-area.not-found {
    border: dashed 1px #a31a27; }
    .search-area.not-found > h3 {
      color: #a31a27; }

